import React, { useState } from "react";
import Footer from "./Footer";
import Header from "./Header2";
import CreateNote from "./CreateNote1";
import Note from "./Note2";
import "./index.css";

const App = () => {

    const [addItem, setAddItem] = useState([]);

    const addNote = (note) => {
        
        setAddItem((prevData) => {
            return [...prevData, note];
        })
    };

    const onDelete = (id) => {
        setAddItem((olddata) =>
            olddata.filter((currdata, indx) => {
                return indx !== id;
            })
        );
    };

    return (
        <>
            <Header />
            <CreateNote passNote={addNote} />

            {addItem.map((val, index) => {
                return (<Note
                    key={index}
                    id={index}
                    title={val.title}
                    content={val.content}
                    deleteItem={onDelete}
                />
                );
            })}

            <Footer />
        </>
    );

};

export default App;